// ui-btn (new)
.ui-btn {
    cursor: pointer;
    position: relative;
    display: inline-flex;
    gap: rem(4px);
    align-items: center;
    justify-content: center;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-decoration: none;
    border-radius: rem(100px);
    font-feature-settings: 'clig' off, 'liga' off;
    transition: background cubic-bezier(0.4, 0, 0.2, 1) 0.2s,
        color cubic-bezier(0.4, 0, 0.2, 1) 0.2s, border-color cubic-bezier(0.4, 0, 0.2, 1) 0.2s,
        font-weight cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
    border: 1px solid currentColor;
    @include font-weight(bold);
    // size
    &.size-small {
        padding: rem(6px - 1px) rem(12px - 1px);
        @include font-size(12px, 16px);
        &:before,
        &:after {
            width: rem(12px);
            height: rem(12px);
        }
    }
    &.size-medium {
        padding: rem(6px - 1px) rem(16px - 1px);
        @include font-size(14px, 20px);
        &:before,
        &:after {
            width: rem(12px);
            height: rem(12px);
        }
    }
    &.size-large {
        padding: rem(10px - 1px) rem(20px - 1px);
        @include font-size(14px, 20px);
        &:before,
        &:after {
            width: rem(16px);
            height: rem(16px);
        }
    }
    // disabled
    a[aria-disabled='true'] {
        pointer-events: none;
    }
    &:disabled,
    &[aria-disabled='true'] {
        cursor: not-allowed !important;
        &.type-default,
        &.type-primary {
            &,
            &:focus,
            &:hover {
                background: $color-gray-dd !important;
                border-color: $color-gray-dd !important;
                color: $color-gray-8f !important;
                &:before,
                &:after {
                    opacity: 0.4;
                }
            }
        }
        &.type-outline {
            &,
            &:focus,
            &:hover {
                background: transparent !important;
                border-color: $color-gray-aa !important;
                color: $color-gray-aa !important;
                &:before,
                &:after {
                    opacity: 0.3;
                }
            }
        }
        &.type-text {
            &,
            &:focus,
            &:hover {
                color: $color-gray-aa;
                &.icon-left,
                &.icon-right,
                &.icon-popup,
                &.icon-share,
                &.icon-download,
                &[target='_blank'] {
                    &:before,
                    &:after {
                        opacity: 0.3;
                    }
                }
            }
        }
        .theme-darkgray &,
        &.theme-darkgray,
        .theme-dark &,
        &.theme-dark {
            &.type-default,
            &.type-primary {
                &,
                &:focus,
                &:hover {
                    background: $color-gray-55 !important;
                    border-color: $color-gray-55 !important;
                    color: $color-gray-8f !important;
                }
            }
            &.type-outline {
                &,
                &:focus,
                &:hover {
                    background: transparent !important;
                    border-color: $color-gray-75 !important;
                    color: $color-gray-75 !important;
                    &:before,
                    &:after {
                        opacity: 0.4;
                    }
                }
            }
            &.type-text {
                &,
                &:focus,
                &:hover {
                    color: $color-gray-75;
                    &.icon-left,
                    &.icon-right,
                    &.icon-popup,
                    &.icon-share,
                    &.icon-download,
                    &[target='_blank'] {
                        &:before,
                        &:after {
                            opacity: 0.4;
                        }
                    }
                }
            }
        }
    }
    // type
    &.type-default {
        background: $color-primary-black;
        border-color: $color-primary-black;
        color: $color-primary-white;
        &:focus,
        &:hover {
            background: $color-gray-55;
            border-color: $color-gray-55;
            color: $color-gray-ee;
        }
    }
    &.type-primary {
        background: $color-secondaryui-skyblue;
        border-color: $color-secondaryui-skyblue;
        color: $color-primary-white;
        &:focus,
        &:hover {
            background: $color-secondaryui-skyblue-dark;
            border-color: $color-secondaryui-skyblue-dark;
            color: $color-gray-ee;
        }
    }
    &.type-outline {
        background: transparent;
        border-color: $color-primary-black;
        color: $color-primary-black;
        &:focus,
        &:hover {
            background: rgba(0, 0, 0, 0.05);
            border-color: $color-gray-55;
            color: $color-gray-55;
        }
    }
    &.type-text {
        height: auto;
        padding: 0 0 1px 0 !important;
        border: none !important;
        border-radius: 0;
        background-color: transparent;
        color: $color-primary-black;
        &:not(.icon-right):not(.icon-left):not(.icon-popup):not(.icon-share):not(
                .icon-download
            ):not([target='_blank']) {
            &:after {
                content: '';
                position: absolute;
                bottom: 0;
                width: 100%;
                left: 50%;
                transform: translateX(-50%);
                height: 2px;
                overflow: hidden;
                color: inherit;
                background-color: currentColor;
            }
        }
        &:focus,
        &:hover {
            color: $color-gray-55;
        }
    }
    // theme
    .theme-darkgray &,
    &.theme-darkgray,
    .theme-dark &,
    &.theme-dark {
        &.type-default {
            background: $color-primary-white;
            border-color: $color-primary-white;
            color: $color-primary-black;
            &:focus,
            &:hover {
                background: $color-gray-dd;
                border-color: $color-gray-dd;
                color: $color-primary-black;
            }
        }
        &.type-outline {
            background: transparent;
            border-color: $color-primary-white;
            color: $color-primary-white;
            &:focus,
            &:hover {
                background: rgba(255, 255, 255, 0.15);
                border-color: $color-gray-dd;
                color: $color-gray-dd;
            }
        }
        &.type-text {
            color: $color-primary-white;
            &:after {
                background-color: $color-primary-white;
            }
            &:focus,
            &:hover {
                color: $color-gray-dd;
                &:after {
                    background-color: $color-gray-dd;
                }
            }
        }
    }
    // icon
    &:before,
    &:after {
        width: 16px;
        height: 16px;
    }
    &.icon-left:before,
    &.icon-right:after,
    &.icon-popup:after,
    &.icon-share:after,
    &.icon-download:after,
    &[target='_blank']:after {
        content: '';
        color: inherit;
        transition: background cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
    }
    &:focus,
    &:hover {
        &.icon-left:before,
        &.icon-right:after,
        &.icon-popup:after,
        &.icon-share:after,
        &.icon-download:after,
        &[target='_blank']:after {
            opacity: 0.9;
        }
    }
    &[target='_blank']:before,
    &.icon-share:before,
    &.icon-download:before,
    &.icon-popup:before {
        display: none !important;
    }

    // default icon
    &.icon-left,
    &.icon-right,
    &[target='_blank'] {
        @include add-icon-default($icon-outlink-bold-black, $icon-outlink-bold-white);
    }
    &.icon-popup {
        @include add-icon-default($icon-popup-bold-black, $icon-popup-bold-white);
    }
    &.icon-share {
        @include add-icon-default($icon-share-bold-black, $icon-share-bold-white);
    }
    &.icon-download {
        @include add-icon-default($icon-download-bold-black, $icon-download-bold-white);
    }
}

// btn-group (old)
.btn-group {
    padding-bottom: 80px;
    @media screen and (max-width: 1023px) {
        padding-bottom: 60px;
    }
    &.btn-center {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        margin-top: 24px;
    }
    & + .btn-group {
        margin-top: 20px;
    }
}
