@use 'sass:list';

// 기존에 페이지에서는 normal, bold, 400, 500, 600, 700 사용되었고,
// 디지인에는 400, 666, 700 사용됨...
$font-bold: 700;
$font-normal: 400;

@mixin font-size($size, $line: null) {
    // font-feature-settings: 'clig' off, 'liga' off;
    font-size: rem($size);
    @if ($line != null) {
        line-height: calc($line / $size);
    }
}
@mixin font-family($font) {
    @if ($font == SSS) {
        font-family: 'Samsung Sharp Sans', sans-serif;
    }
}
@mixin font-weight($weight) {
    @if ($weight == bold) {
        font-weight: $font-bold;
    }
    @if ($weight == normal) {
        font-weight: $font-normal;
    }
}

