.component-box {
    &:not(
            .normal-padding-top,
            .wide-padding-top,
            .narrow-padding-bottom,
            .normal-padding-bottom,
            .wide-padding-bottom,
            .narrow-margin-top,
            .normal-margin-top,
            .wide-margin-top,
            .narrow-margin-bottom,
            .normal-margin-bottom,
            .wide-margin-bottom,
            .CO46_margin
        ) {
        margin-top: -1px !important;
        margin-bottom: -1px !important;
        padding-top: 1px !important;
        padding-bottom: 1px !important;
    }
}
