// pc, tablet, mobile only
.pc-only {
    display: block;
    @media screen and (max-width: 1023px) {
        display: none;
    }
    @media screen and (max-width: 767px) {
        display: none;
    }
}
.ta-only {
    display: none;
    @media screen and (max-width: 1023px) {
        display: block;
    }
    @media screen and (max-width: 767px) {
        display: none;
    }
}
.mo-only {
    display: none;
    @media screen and (max-width: 1023px) {
        display: none;
    }
    @media screen and (max-width: 767px) {
        display: block;
    }
}
// pc, tablet, mobile hidden
.pc-hidden {
    display: none;
    @media screen and (max-width: 1023px) {
        display: block;
    }
    @media screen and (max-width: 767px) {
        display: block;
    }
}
.ta-hidden {
    display: block;
    @media screen and (max-width: 1023px) {
        display: none;
    }
    @media screen and (max-width: 767px) {
        display: block;
    }
}
.mo-hidden {
    display: block;
    @media screen and (max-width: 1023px) {
        display: block;
    }
    @media screen and (max-width: 767px) {
        display: none;
    }
}
