// icon list
$icon-popup-bold-white: '#{$icon-url}icon-popup-bold-white.svg';
$icon-popup-bold-black: '#{$icon-url}icon-popup-bold-black.svg';
$icon-outlink-bold-white: '#{$icon-url}icon-outlink-bold-white.svg';
$icon-outlink-bold-black: '#{$icon-url}icon-outlink-bold-black.svg';
$icon-forward-bold-white: '#{$icon-url}icon-forward-bold-white.svg';
$icon-forward-bold-black: '#{$icon-url}icon-forward-bold-black.svg';
$icon-next-bold-white: '#{$icon-url}icon-next-bold-white.svg';
$icon-next-bold-black: '#{$icon-url}icon-next-bold-black.svg';
$icon-delete-bold-white: '#{$icon-url}icon-delete-bold-white.svg';
$icon-delete-bold-black: '#{$icon-url}icon-delete-bold-black.svg';
$icon-share-bold-white: '#{$icon-url}icon-share-bold-white.svg';
$icon-share-bold-black: '#{$icon-url}icon-share-bold-black.svg';
$icon-download-bold-white: '#{$icon-url}icon-download-bold-white.svg';
$icon-download-bold-black: '#{$icon-url}icon-download-bold-black.svg';
$icon-next-regular-white: '#{$icon-url}icon-next-regular-white.svg';
$icon-next-regular-black: '#{$icon-url}icon-next-regular-black.svg';

// mixins
@mixin add-icon($icon) {
    &:before,
    &:after {
        background: url($icon) no-repeat center center;
        background-size: 100%;
    }
}
@mixin add-icon-default($black-icon, $white-icon, $case: all) {
    &:before,
    &:after,
    .theme-darkgray &.type-text:before,
    .theme-darkgray &.type-text:after,
    &.theme-darkgray.type-text:before,
    &.theme-darkgray.type-text:after,
    .theme-darkgray &.type-outline:before,
    .theme-darkgray &.type-outline:after,
    &.theme-darkgray.type-outline:before,
    &.theme-darkgray.type-outline:after,
    .theme-dark &.type-text:before,
    .theme-dark &.type-text:after,
    &.theme-dark.type-text:before,
    &.theme-dark.type-text:after,
    .theme-dark &.type-outline:before,
    .theme-dark &.type-outline:after,
    &.theme-dark.type-outline:before,
    &.theme-dark.type-outline:after {
        background: url($white-icon) no-repeat center center;
        background-size: 100%;
    }
    &.type-text:before,
    &.type-text:after,
    &.type-outline:before,
    &.type-outline:after,
    .theme-darkgray &.type-default:before,
    .theme-darkgray &.type-default:after,
    &.theme-darkgray.type-default:before,
    &.theme-darkgray.type-default:after,
    .theme-dark &.type-default:before,
    .theme-dark &.type-default:after,
    &.theme-dark.type-default:before,
    &.theme-dark.type-default:after {
        background: url($black-icon) no-repeat center center;
        background-size: 100%;
    }
    &:disabled,
    &[aria-disabled='true'] {
        &.type-text,
        &.type-default,
        &.type-primary {
            &:before,
            &:after {
                background: url($black-icon) no-repeat center center;
                background-size: 100%;
            }
        }
        .theme-darkgray &,
        &.theme-darkgray,
        .theme-dark &,
        &.theme-dark {
            &.type-text,
            &.type-default,
            &.type-primary {
                &:before,
                &:after {
                    background: url($white-icon) no-repeat center center;
                    background-size: 100%;
                }
            }
        }
    }
    // &.type-outline {
    //     &:before,
    //     &:after {
    //         background: url($black-icon) no-repeat center center;
    //         background-size: 100%;
    //     }
    // }
