.common-popup-container {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    background-color: rgba($color-primary-black, 0.6);
    + {
        .common-popup-container {
            background-color: transparent;
        }
    }
    .common-popup-wrapper {
        width: 354px;
        min-width: 0;
        background-color: $color-primary-white;
        border-radius: 20px;
        position: relative;
        padding-top: 32px;
        text-align: center;
        @media screen and (max-width: 1023px) {
            width: 316px;
        }
        @media screen and (max-width: 767px) {
            width: 278px;
            padding-top: 28px;
        }
    }
    .common-popup-header {
        font-size: 2.4rem;
        line-height: 1.42;
        padding: 0 24px;
        font-weight: 700;
        margin-bottom: 8px;
        @include font-family(SSS);
        @media screen and (max-width: 1023px) {
            font-size: 2rem;
            line-height: 1.4;
        }
        @media screen and (max-width: 767px) {
            font-size: 1.6rem;
            line-height: 1.44;
        }
    }
    .common-popup-body {
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
        overscroll-behavior-x: contain;
        max-height: 200px;
        padding: 0 24px;
        font-size: 1.6rem;
        line-height: 1.44;
        padding-bottom: 32px;
        border-bottom: 1px solid #ddd;
        @media screen and (max-width: 1023px) {
            font-size: 1.4rem;
            line-height: 1.43;
        }
        @media screen and (max-width: 767px) {
            font-size: 1.2rem;
            line-height: 1.33;
            padding-bottom: 28px;
        }
    }
    .common-popup-footer {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        > * {
            -webkit-box-flex: 1;
            -ms-flex: 1 1 100%;
            flex: 1 1 100%;
            width: 100%;
            display: block;
            padding: 20px 24px;
            font-size: 1.8rem;
            line-height: 1.39;
            font-weight: 500;
            color: $color-primary-black;
            border-bottom: 1px solid #ddd;
            @include font-family(SSS);
            &:last-child {
                border-bottom: none;
            }
            @media screen and (max-width: 1023px) {
                font-size: 1.6rem;
                line-height: 1.44;
                padding-top: 16px;
                padding-bottom: 16px;
            }
            @media screen and (max-width: 767px) {
                font-size: 1.4rem;
                line-height: 1.43;
            }
        }
    }
}

// popup 예외케이스 (기존 common.css에 존재함, 메인 등에서 rawcode로 비디오 레이어 팝업 등 사용됨)
div[class$='_value-proposition__popup'] {
    z-index: 300 !important;
}

html.popupOpen {
    overflow-y: hidden !important;
    touch-action: none !important;
}

// component popup
.component-popup {
    .ui-popup {
        display: flex;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 999;
        align-items: center;
        justify-content: center;
        width: 100vw;
        height: 100vh;
        padding: rem(80px) rem(32px);
        // background-color: rgba(0, 0, 0, 0.7);
        background: rgba($color-primary-black, 0.7);
        opacity: 1;
        transition: opacity easing();
        transition-delay: 500ms;
        &[aria-hidden='true'] {
            display: none !important;
            opacity: 0;
        }
        @include screen(M) {
            padding: rem(60px) rem(24px);
        }
        .bg {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
        .popup_contents {
            overflow: hidden;
            overflow-y: auto;
            position: relative;
            width: 100%;
            max-width: rem(1156px);
            min-height: rem(650px);
            max-height: 100%;
            padding: rem(72px) rem(60px) rem(60px);
            height: auto;
            border-radius: rem(30px);
            background-color: #fff;
            color: #000;
            .btn_close {
                position: absolute;
                top: rem(24px);
                right: rem(24px);
                width: rem(24px);
                height: rem(24px);
                background: url($icon-delete-bold-black);
                background-position: center;
                background-size: 100% auto;
            }
            .popup-title {
                @include font-family(SSS);
                @include font-size(36px, 44px);
                @include font-weight(bold);
                margin-bottom: rem(32px);
            }
            .popup_contents-inner {
                display: flex;
            }

            @include screen(T) {
                .popup-title {
                    @include font-size(28px, 38px);
                    margin-bottom: rem(24px);
                }
            }
            @include screen(M) {
                max-width: rem(768px);
                padding: rem(64px) rem(24px) rem(20px);
                height: auto;
                min-height: auto;
                max-height: none;
                border-radius: rem(20px);
                .popup-title {
                    @include font-size(20px, 27px);
                    margin-bottom: rem(16px);
                }
                .popup_contents-inner {
                    flex-direction: column;
                }
            }
        }

        &[data-popup='share-popup'] {
            .popup_contents {
                min-height: unset;
                width: 100%;
                max-width: 448px;
                padding: 0 24px 24px;
                * {
                    color: #000;
                }
                .popup-title {
                    font-family: 'Samsung Sharp Sans';
                    font-size: 24px;
                    font-weight: 700;
                    line-height: 1.3334;
                    text-align: left;
                    padding-top: 24px;
                    padding-right: 48px;
                    margin-bottom: 16px;
                }
                .popup_contents-inner {
                    display: flex;
                    justify-content: center;
                    padding: 20px 0;
                }
                .sns-list {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 16px;
                }
                .sns-icon {
                    display: block;
                    width: rem(72px);
                    height: rem(72px);
                    background-position: center;
                    background-size: contain;
                    background-repeat: no-repeat;
                    &.facebook {
                        // background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-share-facebook-white.svg');
                        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-share-facebook-black.svg');
                    }
                    &.instagram {
                        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-share-instagram-black.svg');
                    }
                    &.twitter-x {
                        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-share-twitter-x-white.svg');
                    }
                    &.linked-in {
                        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-share-linked-in-black.svg');
                    }
                }
                .sns-item {
                    .btn-sns-link {
                        display: flex;
                        flex-direction: column;
                        gap: rem(10px);
                        justify-content: center;
                        align-items: center;
                    }
                }
                .sns-title {
                    display: block;
                    width: rem(80px);
                    @include font-size(12px, 20px);
                    text-align: center;
                }
                @include screen(TM) {
                    .popup-title {
                        @include font-size(20px, 27px);
                    }
                    .popup_contents-inner {
                        display: flex;
                        justify-content: center;
                        padding: rem(12px) 0 rem(4px);
                    }
                    .sns-list {
                        gap: rem(12px);
                    }
                    .sns-icon {
                        width: rem(48px);
                        height: rem(48px);
                    }
                    .sns-title {
                        display: block;
                        width: rem(64px);
                        @include font-size(10px, 14px);
                    }
                }
                @include screen(M) {
                    .popup-title {
                        @include font-size(16px, 22px);
                    }
                    .popup_contents-inner {
                        padding: rem(4px) 0 rem(2px);
                    }
                    .sns-list {
                        gap: rem(8px);
                    }
                }
            }
        }
    }
}
