.checkbox {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    label {
        word-break: break-all;
        padding-left: 32px;
        -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
        flex: 1 1 auto;
        position: relative;
        height: 24px;
    }
    input[type='checkbox'] {
        border: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        + {
            label {
                &::after {
                    position: absolute;
                    top: 10px;
                    left: 10px;
                    display: block;
                    content: '';
                    border-radius: 25px;
                    -moz-border-radius: 25px;
                    -webkit-border-radius: 25px;
                    -webkit-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                    -o-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                    transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                    background: #2189ff url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-checkbox.svg') center no-repeat !important;
                    width: 0;
                    height: 0;
                }
                &::before {
                    position: absolute;
                    top: 0;
                    left: 0;
                    display: block;
                    content: '';
                    border: 1px solid #555;
                    border-radius: 25px;
                    -moz-border-radius: 25px;
                    -webkit-border-radius: 25px;
                    width: 20px;
                    height: 20px;
                    -webkit-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                    -o-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                    transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                }
            }
        }
        &:checked {
            + {
                label {
                    &::before {
                        border: none;
                    }
                    &::after {
                        width: 20px;
                        height: 20px;
                        top: 0;
                        left: 0;
                    }
                }
            }
            &:active {
                + {
                    label {
                        &::before {
                            background: rgba(0, 111, 242, 0.08);
                            -moz-background: rgba(0, 111, 242, 0.08);
                            -webkit-background: rgba(0, 111, 242, 0.08);
                            border: 1px solid #006ff2;
                        }
                    }
                }
            }
            &:focus {
                + {
                    label {
                        &::before {
                            background: rgba(0, 111, 242, 0.08);
                            border: 1px solid #006ff2;
                        }
                    }
                }
            }
        }
        &:disabled {
            + {
                label {
                    &::before {
                        border-color: $color-gray-dd;
                        -moz-border-color: #ddd;
                        -webkit-border-color: #ddd;
                    }
                    color: $color-gray-dd;
                    cursor: not-allowed;
                }
            }
            &:checked {
                + {
                    label {
                        &::before {
                            border: none;
                        }
                        &:after {
                            background: #aaa url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-checkbox.svg') center no-repeat !important;
                            width: 20px;
                            height: 20px;
                            border: 0;
                            -moz-border: 0;
                            -webkit-border: 0;
                        }
                    }
                }
            }
        }
    }
    &:hover {
        input[type='checkbox'] {
            + {
                label {
                    &::before {
                        border-color: $color-secondaryui-skyblue-dark;
                        -moz-border-color: #006ff2;
                        -webkit-border-color: #006ff2;
                        background: rgba(0, 111, 242, 0.08);
                    }
                }
            }
            &:disabled {
                + {
                    label {
                        &::before {
                            opacity: 1;
                            border-color: $color-gray-dd;
                            -moz-border-color: #ddd;
                            -webkit-border-color: #ddd;
                            background: none;
                        }
                    }
                }
                &:checked {
                    + {
                        label {
                            &::after {
                                background-color: $color-gray-aa;
                            }
                        }
                    }
                }
            }
            &:checked {
                + {
                    label {
                        &::after {
                            background: #006ff2 url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-checkbox.svg') center no-repeat !important;
                            width: 20px;
                            height: 20px;
                        }
                    }
                }
            }
        }
    }
    &.dark-bg {
        input[type='checkbox'] {
            + {
                label {
                    &::before {
                        border-color: $color-gray-dd;
                        -moz-border-color: #ddd;
                        -webkit-border-color: #ddd;
                    }
                }
            }
            &:disabled {
                + {
                    label {
                        &::before {
                            border-color: $color-gray-55;
                            -moz-border-color: #555;
                            -webkit-border-color: #555;
                        }
                        &:checked {
                            &::after {
                                background-color: $color-gray-75;
                                -moz-background-color: #757575;
                                -webkit-background-color: #757575;
                            }
                        }
                    }
                }
            }
        }
        &:hover.dark-bg {
            input[type='checkbox'] {
                + {
                    label {
                        &::before {
                            border-color: $color-secondaryui-skyblue-dark;
                            -moz-border-color: #006ff2;
                            -webkit-border-color: #006ff2;
                        }
                    }
                }
                &:disabled {
                    + {
                        label {
                            &::before {
                                border-color: $color-gray-55;
                                -moz-border-color: #555;
                                -webkit-border-color: #555;
                            }
                        }
                    }
                }
            }
        }
    }
}

.radio {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    label {
        padding-left: 32px;
        position: relative;
        height: 24px;
    }
    input[type='radio'] {
        width: 0;
        height: 0;
        border: none;
        position: absolute;
        top: 0;
        left: 0;
        + {
            label {
                &::before {
                    position: absolute;
                    top: 0;
                    left: 0;
                    display: block;
                    content: '';
                    background: none;
                    border: 1px solid #555;
                    border-radius: 25px;
                    -moz-border-radius: 25px;
                    -webkit-border-radius: 25px;
                    width: 20px;
                    height: 20px;
                    -webkit-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                    -o-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                    transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                }
                &::after {
                    position: absolute;
                    top: 10px;
                    left: 10px;
                    display: block;
                    content: '';
                    background: #2189ff;
                    border-radius: 25px;
                    -moz-border-radius: 25px;
                    -webkit-border-radius: 25px;
                    width: 0;
                    height: 0;
                    -webkit-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                    -o-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                    transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.1s;
                }
            }
        }
        &:active {
            + {
                label {
                    &::before {
                        border-color: $color-secondaryui-skyblue-dark;
                        -moz-border-color: #006ff2;
                        -webkit-border-color: #006ff2;
                    }
                }
            }
        }
        &:focus {
            + {
                label {
                    &::before {
                        border-color: $color-secondaryui-skyblue-dark;
                        -moz-border-color: #006ff2;
                        -webkit-border-color: #006ff2;
                    }
                }
            }
        }
        &:checked {
            + {
                label {
                    &::before {
                        border-color: $color-secondaryui-skyblue;
                        -moz-border-color: #006ff2;
                        -webkit-border-color: #006ff2;
                    }
                    &::after {
                        width: 12px;
                        height: 12px;
                        top: 4px;
                        left: 4px;
                    }
                }
            }
            &:disabled {
                + {
                    label {
                        &::after {
                            background: #aaa;
                        }
                    }
                }
            }
        }
        &:disabled {
            + {
                label {
                    &::before {
                        border-color: $color-gray-dd;
                        -moz-border-color: #ddd;
                        -webkit-border-color: #ddd;
                    }
                }
            }
        }
    }
    &:hover {
        input[type='radio'] {
            &::before {
                border-color: $color-secondaryui-skyblue;
            }
            &:checked {
                + {
                    label {
                        &::after {
                            background: #006ff2;
                            width: 12px;
                            height: 12px;
                        }
                    }
                }
            }
            &:disabled {
                + {
                    label {
                        &::before {
                            border-color: $color-gray-dd;
                            -webkit-border-color: #ddd;
                        }
                        &::after {
                            width: 0;
                            height: 0;
                        }
                    }
                }
                &:checked {
                    + {
                        label {
                            &::after {
                                background: #aaa;
                                width: 12px;
                                height: 12px;
                            }
                        }
                    }
                }
            }
        }
    }
    &.dark-bg {
        input[type='radio'] {
            + {
                label {
                    &::before {
                        border-color: $color-gray-dd;
                        -moz-border-color: #ddd;
                        -webkit-border-color: #ddd;
                    }
                }
            }
            &:checked {
                + {
                    label {
                        &::before {
                            border-color: $color-secondaryui-skyblue;
                            -moz-border-color: #2189ff;
                            -webkit-border-color: #2189ff;
                        }
                    }
                }
            }
            &:disabled {
                + {
                    label {
                        &::before {
                            border-color: $color-gray-aa;
                            -moz-border-color: #aaa;
                            -webkit-border-color: #aaa;
                        }
                    }
                }
            }
        }
        &:hover {
            input[type='radio'] {
                + {
                    label {
                        &::before {
                            border-color: $color-secondaryui-skyblue-dark;
                            -moz-border-color: #006ff2;
                            -webkit-border-color: #006ff2;
                        }
                    }
                }
                &:disabled {
                    + {
                        label {
                            &::before {
                                border-color: $color-gray-aa;
                                -moz-border-color: #aaa;
                                -webkit-border-color: #aaa;
                            }
                        }
                    }
                }
            }
        }
    }
}

.switch {
    display: inline-block;
    position: relative;
    padding: 2px 1px;
    input {
        cursor: pointer;
        position: absolute;
        top: 2px;
        left: 1px;
        width: 40px;
        height: 24px;
        z-index: 1;
        border: 0;
        + {
            .switch-control {
                position: relative;
                width: 40px;
                height: 24px;
                display: inline-block;
                vertical-align: middle;
                border: 2px solid #757575;
                border-radius: 24px;
                background-color: $color-gray-75;
                -webkit-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
                -o-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
                transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
                &::after {
                    content: '';
                    display: block;
                    width: 20px;
                    height: 20px;
                    background-color: $color-primary-white;
                    border-radius: 50%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    -webkit-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
                    -o-transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
                    transition: all cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
                }
                + {
                    label {
                        padding-left: 8px;
                    }
                }
            }
        }
        &:checked {
            + {
                .switch-control {
                    border-color: $color-secondaryui-skyblue;
                    background-color: $color-secondaryui-skyblue;
                    &:after {
                        left: 16px;
                    }
                }
            }
        }
        &:disabled {
            + {
                .switch-control {
                    background-color: $color-gray-75;
                    border-color: $color-gray-75;
                }
            }
        }
        &:hover {
            + {
                .switch-control {
                    background-color: $color-secondaryui-skyblue-dark;
                    border-color: $color-secondaryui-skyblue-dark;
                }
            }
            &:checked {
                + {
                    .switch-control {
                        background-color: $color-secondaryui-skyblue-dark;
                        border-color: $color-secondaryui-skyblue-dark;
                    }
                }
            }
            &:disabled {
                + {
                    .switch-control {
                        background-color: $color-gray-75;
                        border-color: $color-gray-75;
                    }
                }
            }
        }
    }
    &.switch-light {
        input {
            + {
                .switch-control {
                    border-color: $color-gray-dd;
                    background-color: $color-gray-dd;
                }
            }
            &:disabled {
                + {
                    .switch-control {
                        background-color: $color-gray-dd;
                        border-color: $color-gray-dd;
                    }
                }
                &:hover {
                    + {
                        .switch-control {
                            background-color: $color-gray-dd;
                            border-color: $color-gray-dd;
                        }
                    }
                }
            }
        }
    }
}
