// skip navi
#skipnavi {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    margin-left: 0;
    z-index: 99999;
    color: $color-primary-white;
    a {
        position: absolute;
        -webkit-transform: translate(-100%, -100%);
        -ms-transform: translate(-100%, -100%);
        transform: translate(-100%, -100%);
        display: block;
        width: 100%;
        text-align: center;
        font-size: 1.4em;
        background-color: $color-primary-black;
        &:focus-visible {
            position: relative;
            -webkit-transform: none;
            -ms-transform: none;
            transform: none;
            z-index: 9999;
            width: 100%;
            padding: 10px;
            text-decoration: none;
        }
        &:focus {
            position: relative;
            -webkit-transform: none;
            -ms-transform: none;
            transform: none;
            z-index: 9999;
            width: 100%;
            padding: 10px;
            text-decoration: none;
        }
        &:active {
            position: relative;
            -webkit-transform: none;
            -ms-transform: none;
            transform: none;
            z-index: 9999;
            width: 100%;
            padding: 10px;
            text-decoration: none;
            color: inherit;
        }
        &:link {
            color: inherit;
        }
        &:visited {
            color: inherit;
        }
        &:hover {
            color: inherit;
        }
    }
}
