// alert toast msg
.alert-toast-msg {
    position: fixed;
    left: 50%;
    bottom: 24px;
    height: 48px;
    padding: 0 24px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    line-height: 1.33;
    font-size: 14px;
    color: #fff;
    min-width: 448px;
    -webkit-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
    border-radius: 6px;
    background-color: rgba(51, 51, 51, 0.8);
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
    z-index: 100;
    opacity: 1;
    @media screen and (max-width: 767px) {
        min-width: 312px;
    }
    &.hide {
        opacity: 0;
    }
}
