// input-cover
.input-cover {
    position: relative;
    text-align: left;
    height: 78px;
    padding-top: 22px;
    input[type='text'] {
        & + .single-line-label {
            font-size: 1.8rem;
            cursor: text;
            -webkit-transition: all 0.2s ease-out 0s;
            -o-transition: all 0.2s ease-out 0s;
            transition: all 0.2s ease-out 0s;
            -webkit-transform: translate(0px, 0px);
            -ms-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
        }
    }
    &--focus {
        input[type='text'] {
            & + .single-line-label {
                font-size: 1.4rem;
                -webkit-transform: translate(0px, -20px);
                -ms-transform: translate(0px, -20px);
                transform: translate(0px, -20px);
            }
        }
    }
    .input-element {
        position: absolute;
        right: 0;
        top: 50%;
        -webkit-transform: translate(0, -50%);
        -ms-transform: translate(0, -50%);
        transform: translate(0, -50%);
        &--control {
            .btn--value-delete {
                display: none;
            }
            .on {
                display: inline-block;
            }
        }
    }
    .btn--value-delete {
        border: none;
        background-image: none;
        background-color: #fff;
    }
    .btn--value-delete {
        margin-top: -5px;
        width: 24px;
        height: 24px;
        text-indent: -500rem;
        background: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-bold-action-cancel.svg')
            no-repeat 50% 50% !important;
    }
    .single-line-label {
        position: absolute;
        left: 0;
        top: 24px;
    }
    &__assistive {
        font-size: 1.2rem;
        color: #8f8f8f;
        padding-top: 8px;
    }
    &__success {
        color: #2189ff;
    }
    &__error {
        color: #ef3434;
    }
    &--disabled {
        color: #dddddd;
        input[type='text'] {
            border-bottom: 1px solid #dddddd;
            & + .single-line-label {
                cursor: not-allowed !important;
            }
        }
        .input-cover__assistive {
            color: #dddddd;
        }
    }
    &--dark {
        input[type='text'],
        input[type='password'],
        input[type='number'],
        input[type='tel'],
        input[type='email'],
        input[type='search'],
        input[type='date'] {
            background-color: transparent;
        }
        .btn {
            background-color: #000;
        }
        .btn--value-delete {
            background: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-bold-action-cancel.svg')
                no-repeat 50% 50% !important;
        }
    }
}
.single-line-label {
    cursor: text;
}

