// # rem
@function rem($size) {
    $root-size: 10px;
    @if string == type-of($size) {
        @return $size;
    } @else if 0 == $size {
        @return 0;
    } @else {
        @return calc($size / $root-size * 1rem);
    }
}

// # easing
//  transition: background easing(), color easing(), border-color easing(), font-weight easing();
@function easing($type: default) {
    @if $type == default {
        @return cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
    }
}

// * media query
// $screen : C (custom) | P (pc) | T (tablet) | M (mobile) | PT (pc & tablet) | TM (tablet & mobile)
// $size :
// $direction : min | max
@mixin screen($screen, $size: null, $direction: min) {
    // check value
    @if type-of($screen) != string {
        @warn "#{$screen} : screen - 첫번째 파라미터 $screen은 string 으로 입력해 주세요.";
    }
    @if $screen == C {
        @each $val in $size {
            $to-string: #{$val};
            @if null == str-index($to-string, 'px') {
                @warn "#{$size}' : screen - 두번째 파라미터 $size는 px 단위로 입력해 주세요.";
            }
        }
    }
    @if type-of($direction) != string {
        @warn "#{$direction} : screen - 세번째 파라미터 $direction은 string type으로 입력해 주세요.";
    }
    @if $direction != min and $direction != max {
        @warn "#{$direction} : screen - 세번째 파라미터 $direction은 min 또는 max 만 가능합니다.";
    }

    // viewport
    @if $screen == C {
        @if length($size) > 1 {
            $min: list.nth($size, 1);
            $max: list.nth($size, 2);
            @media (min-width: $min) and (max-width: $max) {
                @content;
            }
        } @else {
            @media (#{$direction}-width: $size) {
                @content;
            }
        }
    } @else if $screen == M {
        // ~ 767
        @media (max-width: ($mobile)) {
            @content;
        }
    } @else if $screen == T {
        // 768 ~ 1023
        @media (min-width: ($mobile + 1)) and (max-width: ($tablet)) {
            @content;
        }
    } @else if $screen == P {
        // 1024 ~
        @media (min-width: ($tablet + 1)) {
            @content;
        }
    } @else if $screen == PT {
        // 769 ~
        @media (min-width: ($mobile + 1)) {
            @content;
        }
    } @else if $screen == TM {
        // 769 ~
        @media (max-width: ($tablet)) {
            @content;
        }
    }

    // orientation
    @else if $screen == portrait {
        @media (orientation: portrait) {
            @content;
        }
    } @else if $screen == landscape {
        @media (orientation: landscape) {
            @content;
        }
    }
}

// * radius
// 영역의 모서리를 둥글게 만들 때 사용
@mixin radius($type: normal) {
    @if $type == normal {
        border-radius: rem($radius-normal-pc); // 30px
        @include screen(T) {
            border-radius: rem($radius-normal-ta); // 25px
        }
        @include screen(M) {
            border-radius: rem($radius-normal-mo); // 20px
        }
    }
    @if $type == small {
        border-radius: rem($radius-small); // 20px
    }
    @if $type == smaller {
        border-radius: rem($radius-smaller); // 10px
    }
    @if $type == tiny {
        border-radius: rem($radius-tiny); // 6px
    }
    @if $type == table {
        border-radius: rem($radius-table); // 4px
    }
}

// aspect-ratio
// 이미지 비디오 등의 가로 세로 비율을 고정 할 때 사용
@mixin aspect-ratio($width, $height) {
    position: relative;
    &:before {
        display: block;
        content: '';
        width: 100%;
        padding-top: calc($height / $width) * 100%;
    }
    > picture,
    > img,
    > video,
    > iframe {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
    }
}

// ** reset-list
// ul, ol 및 하위 li의 스타일을 reset 할 때 사용
@mixin reset-list {
    list-style: none;
    margin: 0;
    padding: 0;
    li {
        margin: 0;
    }
}

// ** ellipsis
// $line : 말줄임을 몇 줄로 적용할지
// $line-height : line-height 값
@mixin ellipsis($line: 1, $line-height: null) {
    overflow: hidden;
    text-overflow: ellipsis;
    @if $line == 1 {
        white-space: nowrap;
        @if $line-height != null {
            height: $line-height;
        }
    }
    @if $line > 1 {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: $line;
        max-height: $line-height * $line * 1em;
    }
}

// clearfix
@mixin clearfix() {
    &:before,
    &:after {
        content: ' ';
        display: table;
    }

    &:after {
        display: block;
        clear: both;
        height: 1px;
        margin-top: -1px;
        visibility: hidden;
    }
    & {
        *zoom: 1;
    }
}

