// pagination
.pagination {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    text-align: center;
    height: 24px;
    margin-bottom: 100px;
    @media screen and (max-width: 1023px) {
        margin-bottom: 80px;
    }
    > a {
        font-family: 'Samsung One', sans-serif;
        font-size: 1.4rem;
        line-height: 1.41;
        font-weight: 400;
        font-weight: 700;
        color: $color-gray-aa;
        display: inline-block;
        width: 39px;
        height: 19px;
        @media screen and (max-width: 1023px) {
            font-size: 12px;
            width: 30px;
            height: 16px;
        }
        @media screen and (max-width: 767px) {
            font-size: 1.2rem;
            width: 34px;
            height: 16px;
        }

        ~ {
            em {
                color: $color-primary-black;
            }
        }
        &:hover {
            color: $color-primary-black;
        }
        &:hover.page-ellipsis {
            color: $color-gray-aa;
        }
    }
    em {
        font-family: 'Samsung One', sans-serif;
        font-size: 1.4rem;
        line-height: 1.41;
        font-weight: 400;
        font-weight: 700;
        color: $color-gray-aa;
        display: inline-block;
        width: 39px;
        height: 19px;
        @media screen and (max-width: 1023px) {
            font-size: 12px;
            width: 30px;
            height: 16px;
        }
        @media screen and (max-width: 767px) {
            font-size: 1.2rem;
            width: 34px;
            height: 16px;
        }
        ~ {
            em {
                color: $color-primary-black;
            }
        }
        &:hover {
            color: $color-primary-black;
        }
        &:hover.page-ellipsis {
            color: $color-gray-aa;
        }
    }
    > a.active {
        color: $color-primary-black;
    }
    em.active {
        color: $color-primary-black;
    }
    > a.active.page-ellipsis {
        color: $color-gray-aa;
    }
    em.active.page-ellipsis {
        color: $color-gray-aa;
    }
    > a.page-btn {
        font-size: 0;
        width: 16px;
        height: 16px;
        margin: 0 6px;
        background-position: center 1px;
        background-size: 100%;
        background-repeat: no-repeat;
        @media screen and (max-width: 767px) {
            width: 12px;
            height: 12px;
            margin: 0 4px;
        }
    }
    em.page-btn {
        font-size: 0;
        width: 16px;
        height: 16px;
        margin: 0 6px;
        background-position: center 1px;
        background-size: 100%;
        background-repeat: no-repeat;
        @media screen and (max-width: 767px) {
            width: 12px;
            height: 12px;
            margin: 0 4px;
        }
    }
    > a.page-btn.first {
        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1.svg') !important;
        &:not([aria-disabled='true']) {
            .active {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1-black.svg') !important;
            }
            &:hover {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1-black.svg') !important;
            }
        }
    }
    em.page-btn.first {
        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1.svg') !important;
        &:not([aria-disabled='true']) {
            .active {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1-black.svg') !important;
            }
            &:hover {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1-black.svg') !important;
            }
        }
    }
    > a.page-btn.first[aria-disabled='true'] {
        cursor: not-allowed;
        pointer-events: none;
    }
    em.page-btn.first[aria-disabled='true'] {
        cursor: not-allowed;
        pointer-events: none;
    }
    > a.page-btn.prev {
        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2.svg') !important;
        &:not([aria-disabled='true']) {
            .active {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2-black.svg') !important;
            }
            &:hover {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2-black.svg') !important;
            }
        }
    }
    em.page-btn.prev {
        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2.svg') !important;
        &:not([aria-disabled='true']) {
            .active {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2-black.svg') !important;
            }
            &:hover {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2-black.svg') !important;
            }
        }
    }
    > a.page-btn.prev[aria-disabled='true'] {
        cursor: not-allowed;
        pointer-events: none;
    }
    em.page-btn.prev[aria-disabled='true'] {
        cursor: not-allowed;
        pointer-events: none;
    }
    > a.page-btn.next {
        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2.svg') !important;
        -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        transform: rotate(180deg);
        background-position: center 0px;
        &:not([aria-disabled='true']) {
            .active {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2-black.svg') !important;
            }
            &:hover {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2-black.svg') !important;
            }
        }
    }
    em.page-btn.next {
        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2.svg') !important;
        -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        transform: rotate(180deg);
        background-position: center 0px;
        &:not([aria-disabled='true']) {
            .active {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2-black.svg') !important;
            }
            &:hover {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow2-black.svg') !important;
            }
        }
    }
    > a.page-btn.next[aria-disabled='true'] {
        cursor: not-allowed;
        pointer-events: none;
    }
    em.page-btn.next[aria-disabled='true'] {
        cursor: not-allowed;
        pointer-events: none;
    }
    > a.page-btn.last {
        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1.svg') !important;
        -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        transform: rotate(180deg);
        background-position: center 0px;
        &:not([aria-disabled='true']) {
            .active {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1-black.svg') !important;
            }
            &:hover {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1-black.svg') !important;
            }
        }
    }
    em.page-btn.last {
        background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1.svg') !important;
        -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        transform: rotate(180deg);
        background-position: center 0px;
        &:not([aria-disabled='true']) {
            .active {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1-black.svg') !important;
            }
            &:hover {
                background-image: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon_paging-arrow1-black.svg') !important;
            }
        }
    }
    > a.page-btn.last[aria-disabled='true'] {
        cursor: not-allowed;
        pointer-events: none;
    }
    em.page-btn.last[aria-disabled='true'] {
        cursor: not-allowed;
        pointer-events: none;
    }
}
