// i-tag
.i-tag {
    width: 40px;
    height: 40px;
    margin-bottom: 12px;
    @media screen and (max-width: 1023px) {
        margin-bottom: 16px;
    }
    &--error-regular {
        display: block;
        background: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-regular-information-error.svg') no-repeat 0 0 !important;
    }
    &--error-bold {
        display: block;
        background: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-bold-information-error.svg') no-repeat 0 0 !important;
    }
}
// no-result
.no-result {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-flow: column;
    flex-flow: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding-top: 60px;
    padding-bottom: 120px;
    &__title {
        font-family: 'Samsung Sharp Sans', sans-serif;
        font-size: 2.4rem;
        line-height: 1.41;
        font-weight: 700;
        letter-spacing: 0.2px;
        color: #8f8f8f;
        @media screen and (max-width: 1023px) {
            font-size: 2rem;
            line-height: 2.8rem;
        }
        @media screen and (max-width: 767px) {
            font-size: 2rem;
            line-height: 2.7rem;
        }
    }
    &__text {
        line-height: 2.1rem;
        color: #8f8f8f;
        @media screen and (max-width: 767px) {
            font-size: 1.2rem;
            line-height: 1.6rem;
            color: #8f8f8f;
        }
        @media screen and (max-width: 1023px) {
            line-height: 2.1rem;
            color: #8f8f8f;
        }
    }
}
