.container .footnote,
.static-content .footnote {
    max-width: 1220px;
    padding: 40px 32px;
    margin: 0 auto;
    @media screen and (max-width: 1023px) {
        max-width: 100%;
        padding: 32px;
    }
    @media screen and (max-width: 767px) {
        padding: 24px;
    }
    .footnote__text {
        max-width: 100%;
        padding: 0 0 0;
    }
}
