// drop-down
.drop-down-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.drop-down {
    position: relative;
    margin-left: auto;
    z-index: 10;
    @media screen and (max-width: 1023px) {
        font-size: 1.5rem !important;
    }

    @media screen and (max-width: 767px) {
        font-size: 1.4rem !important;
    }
}

// combobox
.combobox {
    display: none;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    border-radius: 6px;
    padding: 8px 0;
    -webkit-box-shadow: 0 4px 10px -2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 4px 10px -2px rgba(0, 0, 0, 0.2);
    position: absolute;
    right: 5px;
    min-width: 180px;
    z-index: 1000;
    margin-top: 6px;
    background-color: #fff;
    overflow-y: auto;
    max-height: 216px;
    &__option {
        -webkit-box-flex: 0;
        -ms-flex: 0 0 100%;
        flex: 0 0 100%;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        color: #000000;
        width: auto;
        height: 40px;
        @media (hover: hover) {
            &:first-child button:hover {
                border-radius: 6px 6px 0 0;
            }
            &:last-child button:hover {
                border-radius: 0 0 6px 6px;
            }
        }
        &:first-child button:active,
        &:first-child button:focus-visible {
            border-radius: 6px 6px 0 0;
        }
        &:last-child button:active,
        &:last-child button:focus-visible {
            border-radius: 0 0 6px 6px;
        }
        & > button {
            width: 100%;
            padding: 0 40px 0 24px;
            text-align: left;
            position: relative;
            -webkit-transition: background cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
            -o-transition: background cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
            transition: background cubic-bezier(0.4, 0, 0.2, 1) 0.2s;
            @media (hover: hover) {
                &:hover {
                    color: #333333;
                    background-color: #f8f8f8;
                }
            }
            &:active,
            &:focus-visible {
                color: #333333;
                background-color: #f8f8f8;
            }
        }
        &.active {
            font-weight: 700;
            & > button:after {
                position: absolute;
                right: 24px;
                top: 50%;
                margin-top: -8px;
                content: '';
                width: 16px;
                height: 16px;
                background: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-bold-action-done.svg') no-repeat center center !important;
            }
        }
    }
}

// btn--drop-down
.btn--drop-down {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    font-size: 1.8rem;
    font-weight: 700;
    border: none;
    height: 36px;
    padding: 0 24px;
    @media screen and (max-width: 1023px) {
        font-size: 1.5rem !important;
    }

    @media screen and (max-width: 767px) {
        font-size: 1.4rem !important;
    }
    &::after {
        content: '';
        display: block;
        margin-left: 8px;
        width: 16px;
        height: 16px;
        background: url('/etc.clientlibs/semiconductor/designs/global/common/resources/img/icon/icon-bold-navigation-open-down.svg') no-repeat center center !important;
        -webkit-transform: rotate(0deg) !important;
        -ms-transform: rotate(0deg) !important;
        transform: rotate(0deg) !important;
    }
    &-selected::after {
        content: '';
        -webkit-transform: rotate(180deg) !important;
        -ms-transform: rotate(180deg) !important;
        transform: rotate(180deg) !important;
    }
}

.btn--drop-down,
.btn-news-filter__reset,
.news-filter-result,
.btn-news-filter {
    @media screen and (max-width: 1023px) {
        font-size: 15px !important;
    }
    @media screen and (max-width: 767px) {
        font-size: 14px !important;
    }
}
