// data table
.data-table {
    border-bottom: 1px solid #e0e0e0;
    width: 100%;
    table-layout: fixed;
    border-collapse: collapse;
    border-spacing: 0;
    &--dark td {
        background-color: #000;
    }
    caption {
        margin: 0;
        padding: 0;
        width: 1px;
        height: 1px;
        line-height: 0;
        font-size: 0;
        visibility: hidden;
    }
    th,
    td {
        padding: 0 4px;
        height: 30px;
        color: #333333;
        border-top: 1px solid #e0e0e0;
        border-color: #e0e0e0;
    }
    th {
        background-color: #fafafa;
    }
    tbody td + td {
        border-left-width: 1px;
        border-left-style: dotted;
    }
}
